<template>
    <div class="outer">
        <marquee behavior="" direction=""
            >活动异常火爆，请尽快下单，恭喜{{ name }}用户成功下单一辆车</marquee
        >
        <slot>
            <swiper class="swiper" :options="swiperOption" v-if="list.length">
                <!-- 每一屏 -->
                <swiper-slide v-for="item in list" :key="item.id">
                    <img :src="item.imgUrl" />
                </swiper-slide>

                <!-- 小圆点 -->
                <div class="swiper-pagination" slot="pagination"></div>
                <!-- 上一张 -->
                <div class="swiper-button-prev" slot="button-prev"></div>
                <!-- 下一张 -->
                <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
        </slot>
    </div>
</template>

<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
export default {
    name: "SwiperList",
    components: { Swiper, SwiperSlide },
    props: ["list", "options",'name'],
    data() {
        return {
            swiperOption: {
                slidesPerView: 1, //同时展示几张
                spaceBetween: 20, //屏与屏之间的距离
                loop: true, //是否开启循环轮播
                speed: 1000, //切换速度
                autoplay: {
                    delay: 3000, //延迟时间（每一屏看多久）
                    stopOnLastSlide: true, //最后一张是否停止（当开启循环轮播时会失效）
                    disableOnInteraction: false, //产生交互后是否继续轮播
                },
                //小圆点相关配置
                pagination: {
                    el: ".swiper-pagination", //小圆点所在容器
                    clickable: true, //小圆点是否可以点击
                },
                // 上一张、下一张
                navigation: {
                    nextEl: ".swiper-button-next",
                    prevEl: ".swiper-button-prev",
                },
                ...this.options,
            },
        };
    },
};
</script>

<style lang="less" scoped>
 .outer {
    position: relative;
  }
  marquee {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 9;
    font-size: 20px;
    color: white;
    font-weight: 900;
    background-image: linear-gradient(45deg,red,yellow,green);
  }
</style>